body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Header{
  background-color: black;
  color:white;
  padding:20px 70px;
  display:flex;
  justify-content:space-between;
  font-size: 20px;
}
.cart-Icon .counter{
  height:15px;
  width:15px;
  background-color:blue;
  border-radius:50%;
  top:-6px;
  position:relative;
  right:7px;
  justify-content:center;
  align-items:center;
  display:flex;
  font-size: 11px;
  
}
.cart-Icon{
  display:flex;
}
.icon{
  font-size:22px;
}
img{
  height:150px;
}
.cart-wrapper{
  display:inline-flex;
  width:300px;
  box-shadow:0px 0px 7px black;
  border-radius:5px;

}
.image-wrapper{
  width:130px;
  align-items:center;
  justify-content: center;
  padding:10px;
  display:flex;
  border-right:2px solid rgb(136, 133, 133);
}
.content{
  flex-grow:1;
  display:flex;
  flex-direction:column;
  gap:10px;
  padding:10px;
}
.price{
  margin-bottom:auto;
}
.content button{
  background-color:black;
  color:white;
  border-radius:3px;
}
.quantity{
  display:flex;
  align-self:flex-end;
  border-radius:3px;
  border:1px solid grey;

}
.quantity div{
  height:20px;
  width:20px;
  display:flex;
  align-items:center;
  justify-content:center;
}
.quantity > div:first-child,.quantity > button{
  background-color:rgb(4, 4, 78) !important;
  color:white;
  border-radius:0;

}
.quantity > button:disabled{
  opacity: 0.7;
}
main{
  padding:20px;
  display:flex;
  flex-wrap:wrap;
  min-height:80vh;
  align-items:center;
  justify-content:center;
  align-content:center;
  gap:55px;
}
.Foot{
  background-color: rgb(105, 104, 104);
  color:white;
  padding:17px;
 display:flex;align-items:flex-end;
 justify-content:flex-end;
 margin-bottom: none;
 box-sizing:border-box;

}